import React, { useState } from "react";
import { Link } from "react-router-dom";
import { useAuth0 } from "@auth0/auth0-react";
import { Card } from "react-bootstrap";
import Modal from "react-bootstrap/Modal";

import { Button } from "react-bootstrap";
import { LightAsync } from "react-syntax-highlighter";

function Actor({ id, name, age, gender }) {
  const [show, setShow] = useState(false);

  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);
  const { isAuthenticated } = useAuth0();

  const { getAccessTokenSilently } = useAuth0();

  const apiUrl = process.env.REACT_APP_API_URL;

  const removeActor = async (event) => {
    event.preventDefault();

    const token = await getAccessTokenSilently();

    const requestOptions = {
      method: "DELETE",
      headers: {
        Authorization: `Bearer ${token}`,
      },
    };

    await fetch(`${apiUrl}/actors/${id}`, requestOptions)
      .then((response) => response.json())
      .then((data) => {
        console.log(data);
      });
  };

  return (
    <Card style={{ width: "18rem" }}>
      <Card.Body>
        <Card.Title>{name}</Card.Title>
        <Card.Subtitle className="mb-2 text-muted">{age}</Card.Subtitle>
        <Card.Subtitle className="mb-2 text-muted">{gender}</Card.Subtitle>
        <>
          <Button variant="primary" onClick={handleShow}>
            View Actor
          </Button>
          <Modal show={show} onHide={handleClose}>
            <Modal.Header closeButton>
              <Modal.Title>{name}</Modal.Title>
            </Modal.Header>
            <Modal.Body>
              <ul>
                <li>Name : {name}</li>
                <li>Age : {age}</li>
                <li>Gender : {gender}</li>
              </ul>
            </Modal.Body>
            <Modal.Footer>
              <Button variant="secondary" onClick={handleClose}>
                Close
              </Button>
              {isAuthenticated && (
                <Button
                  type="submit"
                  variant="primary"
                  onClick={(event) => {
                    removeActor(event);
                    handleClose();
                  }}
                >
                  Remove
                </Button>
              )}
            </Modal.Footer>
          </Modal>
        </>
      </Card.Body>
    </Card>
  );
}

export default Actor;
