import React, { useState } from "react";
import { Button } from "react-bootstrap";
import Modal from "react-bootstrap/Modal";
import Form from "react-bootstrap/Form";
import { useAuth0 } from "@auth0/auth0-react";

function CreateActor({ actors }) {
  const [show, setShow] = useState(false);

  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);
  const [name, setName] = useState("");
  const [age, setAge] = useState("");
  const [gender, setGender] = useState("");
  const { getAccessTokenSilently } = useAuth0();
  //   const { isAuthenticated } = useAuth0();
  const apiUrl = process.env.REACT_APP_API_URL;

  const createActor = async (event) => {
    console.log("hereeeeeeeeeee");
    event.preventDefault(); //stops the refresh
    console.log("I am here", name);
    const token = await getAccessTokenSilently();

    const requestOptions = {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${token}`,
      },
      body: JSON.stringify({
        name,
        age,
        gender,
      }),
    };
    console.log(">>>>>>>>>>>>>>>>", requestOptions);
    await fetch(`${apiUrl}/actors/create`, requestOptions)
      .then((response) => response.json())
      .then((data) => {
        console.log(data);
      });
  };

  //create actor logic

  return (
    <>
      <Button variant="primary" onClick={handleShow}>
        Create Actor
      </Button>

      <Modal show={show} onHide={handleClose}>
        <Modal.Header closeButton>
          <Modal.Title>Create an Actor</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <Form.Group>
            <Form.Control
              value={name}
              onChange={(event) => setName(event.target.value)}
              type="text"
              placeholder="Name"
            />
            <br />
            <Form.Control
              value={age}
              onChange={(event) => setAge(event.target.value)}
              type="text"
              placeholder="Age"
            />
            <br />
            <Form.Control
              value={gender}
              onChange={(event) => setGender(event.target.value)}
              as="select"
              className="my-1 mr-sm-2"
              id="inlineFormCustomSelectPref"
              custom
            >
              <option value="0">Choose...</option>
              <option value="male">Male</option>
              <option value="female">Female</option>
            </Form.Control>
            <br />
          </Form.Group>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={handleClose}>
            Close
          </Button>
          <Button
            type="submit"
            variant="primary"
            onClick={(event) => {
              createActor(event);
              handleClose();
            }}
          >
            Create
          </Button>
        </Modal.Footer>
      </Modal>
    </>
  );
}

export default CreateActor;

